import React, { useState } from "react";
import { Navbar, Nav, NavDropdown, Container } from "react-bootstrap";
import './Header.css';
import { Link, useNavigate } from "react-router-dom";

function Header() {
  const navigate = useNavigate()
  const [menuOpen, setMenuOpen] = useState(false);

  return (
    <>
      {/* 햄버거 메뉴 클릭 시 나오는 오버레이 드로어 */}
      {menuOpen && (
        <div className="gentle-drawer-bg" onClick={() => setMenuOpen(false)}>
          <nav className="gentle-drawer" onClick={e => e.stopPropagation()}>
            <button className="drawer-close" onClick={() => setMenuOpen(false)}>×</button>
            <Link onClick={() => {navigate("/")}}>메인</Link>
            <Link onClick={() => setMenuOpen(false)}>선글라스</Link>
            <Link onClick={() => setMenuOpen(false)}>안경</Link>
            <Link onClick={() => setMenuOpen(false)}>컬렉션</Link>
            <Link onClick={() => setMenuOpen(false)}>스토어</Link>
            <Link onClick={() => setMenuOpen(false)}>더 알아보기</Link>
          </nav>
        </div>
      )}

      <Navbar expand="lg" className="gentle-navbar">
        <Container fluid>
          {/* 모바일 전용 햄버거 */}
          <button
            className="gentle-hamburger"
            onClick={() => setMenuOpen(true)}
            aria-label="메뉴 열기"
          >
            <img src={process.env.PUBLIC_URL + "/img/menu.png"} alt="menu" />
          </button>
          {/* 왼쪽 메뉴 */}
          <Nav className="me-auto align-items-center gentle-left-nav">
            <NavDropdown title="선글라스" id="nav-dropdown-sunglass">
              <NavDropdown.Item>전체보기</NavDropdown.Item>
              <NavDropdown.Item>포켓 컬렉션</NavDropdown.Item>
              <NavDropdown.Item>베스트셀러</NavDropdown.Item>
              <NavDropdown.Item>신상품</NavDropdown.Item>
              <NavDropdown.Item>2025 컬렉션</NavDropdown.Item>
              <NavDropdown.Item>볼드 컬렉션</NavDropdown.Item>
            </NavDropdown>
            <NavDropdown title="안경" id="nav-dropdown-glasses">
              <NavDropdown.Item>전체보기</NavDropdown.Item>
              <NavDropdown.Item>포켓 컬렉션</NavDropdown.Item>
              <NavDropdown.Item>베스트셀러</NavDropdown.Item>
            </NavDropdown>
            <Nav.Link>컬렉션</Nav.Link>
            <Nav.Link>스토어</Nav.Link>
            <Nav.Link>더 알아보기</Nav.Link>
          </Nav>

          {/* 중앙 로고 */}
          <Navbar.Brand className="mx-auto gentle-logo" onClick={() => {navigate("/")}}>            
            <img src={process.env.PUBLIC_URL + "/img/logo.png"} alt="toplogo"></img>            
          </Navbar.Brand>

          {/* 오른쪽 아이콘 */}
          <Nav className="ms-auto align-items-center gentle-right-nav">
            <Nav.Link>
              <img src={process.env.PUBLIC_URL + "/img/search.svg"} alt="search" className="gentle-icon" />
            </Nav.Link>
            <Nav.Link>
              <img src={process.env.PUBLIC_URL + "/img/user.svg"} alt="user" className="gentle-icon" />
            </Nav.Link>
            <Nav.Link as={Link} to="/cart">
              <img src={process.env.PUBLIC_URL + "/img/bag.svg"} alt="bag" className="gentle-icon" />
            </Nav.Link>
          </Nav>
        </Container>
      </Navbar>
    </>
  );
}

export default Header;