import "./CartModal.css";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { closeModal } from "./store/cartModalSlice";
import { useNavigate } from "react-router-dom";


export default function CartModal() {
  
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const open = useSelector(state => state.cartModal.open);
  const lastItem = useSelector(state => state.cartModal.lastItem); 



  if (!open || !lastItem) return null;
  return (
    <div className="cart-modal">
      <button className="cart-modal-close" onClick={() => dispatch(closeModal())}>×</button>
      <div className="cart-modal-title">
        쇼핑백에 <b>{lastItem.title}</b> 을(를) 추가했습니다.
      </div>
      <div className="cart-modal-bag-title">쇼핑백<sup>2</sup></div>
      <div className="cart-modal-product-row">
        <img className="cart-modal-thumb" src={process.env.PUBLIC_URL + lastItem.thumbnail} alt={lastItem.title} />
        <div>
          <div className="cart-modal-prod-title">{lastItem.title}</div>
          <div className="cart-modal-prod-price">{lastItem.price.toLocaleString()}원</div>
        </div>
      </div>
      <div className="cart-modal-btn-row">
        <button  className="cart-modal-btn" onClick={() => { dispatch(closeModal()); navigate("/cart"); }}>쇼핑백 보기</button>
        <button className="cart-modal-btn cart-modal-btn--pay">결제하기</button>
      </div>
    </div>
  );
}