import React from "react";
import productData from "./data2";
import "./Item2.css";
import { Swiper,SwiperSlide } from "swiper/react";
import {Link} from "react-router-dom";
import "swiper/css";

function Item2() {
  return (
    <div className="item2-wrapper">
      <h2 className="item2-title">TOP 20 베스트셀러</h2>
      <Swiper
        slidesPerView={1}
        spaceBetween={20}
        grabCursor={true}
        breakpoints={{
          640: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          960: {
            slidesPerView: 3,
            spaceBetween: 20,
          },
          1200: {
            slidesPerView: 5,
            spaceBetween: 20,
          },
        }}
        className="item2-swiper"        
      >
        {productData.map((item, i) => (
          <SwiperSlide key={i} className="item2-slide">
            <Link to={`/detail2/${item.id}`}>
             <img src={process.env.PUBLIC_URL + item.thumbnail} alt={item.title} className="item2-img" />
             <h4>{item.title}</h4>
             <p>{item.price.toLocaleString()}원</p>
            </Link>
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}

export default Item2;
