import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  items: [], // [{id, title, price, thumbnail, count}]
};

const cartSlice = createSlice({
  name: "cart",
  initialState,
  reducers: {
    addToCart: (state, action) => {
      const found = state.items.find(item => item.id === action.payload.id);
      if (found) {
        found.count += action.payload.count || 1;
      } else {
        state.items.push({ ...action.payload, count: action.payload.count || 1 });
      }
    },
    removeFromCart: (state, action) => {
      state.items = state.items.filter(item => item.id !== action.payload);
    },
    changeCount: (state, action) => {
      const { id, count } = action.payload;
      const found = state.items.find(item => item.id === id);
      if (found && count > 0) found.count = count;
    },
    clearCart: (state) => {
      state.items = [];
    }
  }
});

export const { addToCart, removeFromCart, changeCount, clearCart } = cartSlice.actions;
export default cartSlice.reducer;