import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  name: "비회원",
  isLogin: false,
};

const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    login: (state, action) => {
      state.name = action.payload.name;
      state.isLogin = true;
    },
    logout: (state) => {
      state.name = "비회원";
      state.isLogin = false;
    }
  }
});

export const { login, logout } = userSlice.actions;
export default userSlice.reducer;