import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import {Link} from "react-router-dom";
import 'swiper/css';
import productData from './data';
import './Item1.css';
import { Scrollbar } from 'swiper/modules';

function Item1() {
  return (
    <section className="item1-section">
      <h2 className="item1-title">새롭게 선보이는 젠틀몬스터 신제품</h2>

      <Swiper spaceBetween={60} slidesPerView={2} loop={false} grabCursor={true} scrollbar={{
          hide: true,
        }}
        modules={[Scrollbar]} >
        {productData.map((item) => (
          <SwiperSlide key={item.id}>
            <Link to={`/detail/${item.id}`} className="product-box">
              <img className="model-img" src={process.env.PUBLIC_URL + item.thumbnail} alt={item.title} />
              <div className="product-info">
                <p className="product-title">{item.title}</p>
                <p className="product-price">₩{item.price.toLocaleString()}</p>
                <button className="add-btn">위시리스트에 추가하기</button>
              </div>
            </Link>
          </SwiperSlide>
        ))}
      </Swiper>
    </section>
  );
}

export default Item1;