import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { useDispatch } from "react-redux";
import { addToCart } from "./store/cartSlice";
import { openModal } from "./store/cartModalSlice"
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import productData from "./data2";
import Header from "./Header";
import "./Detail.css";

function Detail2() {
  const { id } = useParams();
  const item = productData.find(p => p.id === parseInt(id));
  const [openSections, setOpenSections] = useState([]);
  const [selectedColor, setSelectedColor] = useState(0);

  // 반응형: 960px 이하 슬라이드
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 960);
  useEffect(() => {
    const handleResize = () => setIsMobile(window.innerWidth <= 960);
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // 슬라이드 프로그래스바
  const [currentSlide, setCurrentSlide] = useState(0);

  const toggleAccordion = (index) => {
    setOpenSections(prev =>
      prev.includes(index) ? prev.filter(i => i !== index) : [...prev, index]
    );
  };

  const dispatch = useDispatch();
  const handleAddToCart = () => {
    const itemInfo = {
      id: item.id,
      title: item.title,
      price: item.price,
      thumbnail: item.thumbnail,
      count: 1
    };
    dispatch(addToCart(itemInfo));
    dispatch(openModal(itemInfo));
  };

  if (!item) {
    return (
      <>
        <Header />
        <div className="detail-wrapper">
          <div style={{ padding: 80, fontSize: 20 }}>상품 정보를 찾을 수 없습니다.</div>
        </div>
      </>
    );
  }

  return (
    <>
      <Header />
      <div className="detail-wrapper">
        {/* 왼쪽 이미지/동영상 영역 */}
        <div className="detail-images">
          {isMobile ? (
            <>
              <Swiper
                onSlideChange={swiper => setCurrentSlide(swiper.activeIndex)}
                slidesPerView={1}
                spaceBetween={0}
                className="detail-swiper"
              >
                {item.images.map((src, i) =>
                  src.endsWith('.mp4') ? (
                    <SwiperSlide key={i}>
                      <video
                        src={process.env.PUBLIC_URL + src}
                        className="detail-img"
                        muted
                        autoPlay
                        loop
                        playsInline
                        preload="auto"
                      />
                    </SwiperSlide>
                  ) : (
                    <SwiperSlide key={i}>
                      <img
                        src={process.env.PUBLIC_URL + src}
                        alt={`${item.title} 상세이미지 ${i + 1}`}
                        className="detail-img"
                        draggable={false}
                      />
                    </SwiperSlide>
                  )
                )}
              </Swiper>
              {/* 프로그래스바 */}
              <div className="detail-progressbar">
                {item.images.map((_, i) => (
                  <div key={i} className={`detail-bar-dot${i === currentSlide ? " active" : ""}`} />
                ))}
              </div>
            </>
          ) : (
            // PC: 세로 이미지/동영상 리스트
            item.images.map((src, i) =>
              src.endsWith('.mp4') ? (
                <video
                  key={i}
                  src={process.env.PUBLIC_URL + src}
                  className="detail-img"
                  muted
                  autoPlay
                  loop
                  playsInline
                  preload="auto"
                />
              ) : (
                <img
                  key={i}
                  src={process.env.PUBLIC_URL + src}
                  alt={`${item.title} 상세이미지 ${i + 1}`}
                  className="detail-img"
                  draggable={false}
                />
              )
            )
          )}
        </div>

        {/* 오른쪽 정보 영역 */}
        <div className="detail-info">
          <h2 className="detail-title">{item.title}</h2>
          <p className="detail-price">₩{item.price.toLocaleString()}</p>

          <div className="color-picker">
            {item.colors?.map((c, idx) => (
              <div
                key={idx}
                className={`color-box${selectedColor === idx ? " selected" : ""}`}
                style={{ background: c.code }}
                onClick={() => setSelectedColor(idx)}
                title={c.label}
              />
            ))}
          </div>
          <p className="color-label">
            {item.colors?.[selectedColor]?.label || "색상명 미정"}
          </p>
          <button onClick={handleAddToCart} className="cart-btn">쇼핑백에 추가하기</button>

          {/* 아코디언 정보 */}
          <div className="accordion">
            {[
              {
                title: "무료 배송 & 반품",
                content:
                  "젠틀몬스터 공식 온라인 스토어는 무료 배송 및 반품 서비스를 제공합니다. 반품은 제품을 수령하신 날로부터 7일 이내에 접수해 주셔야 합니다. 제품은 사용되지 않은 상태여야 하며, 모든 구성품도 포함하고 있어야 합니다."
              },
              {
                title: "세부 정보",
                content:
                  "제조자 및 수입자: IICOMBINED CO., LTD 제조국명: 중국 본 제품은 피팅이 불가합니다. 본 제품은 회원 전용 제품입니다."
              },
              {
                title: "사이즈 및 핏",
                content: "(이곳에 사이즈 정보를 작성하세요)"
              }
            ].map((section, index) => (
              <div className="accordion-item" key={index}>
                <div className="accordion-header" onClick={() => toggleAccordion(index)}>
                  {section.title} <span>{openSections.includes(index) ? "−" : "+"}</span>
                </div>
                {openSections.includes(index) && (
                  <div className="accordion-body">{section.content}</div>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
    </>
  );
}

export default Detail2;